<template>
	<view class="home">
		<u-swiper :list="advertiseList" name="pic" height="320"></u-swiper>
		
		<view class="nav">
			<view class="nav_item" v-for="(item,index) in navs" :key='index' @click="navClick(item.path)">
				<image :src="item.icon"></image>
				<text>{{item.title}}</text>
			</view>
		</view>
		
		<view class="recommend">
			<view class="tit">推荐专题</view>
			<subject :SubjectList="SubjectList" @itemClick="goDetail"></subject>
		</view>
	</view>
</template>

<script>
	import subject from '../../components/subject-list.vue'
	export default {
		data() {
			return {
				advertiseList:[],
				SubjectList:[],
				navs:[
					{
						icon:'../../static/icon/huati.png',
						title:'专题',
						path:'/pages/subject/subject'
					},
					{
						icon:'../../static/icon/lianxi.png',
						title:'联系我们',
						path:'/pages/our/our'
					},
					{
						icon:'../../static/icon/daka.png',
						title:'每日打卡',
						path:'/pages/clock/clock'
					}
				]
			}
		},
		components:{"subject":subject},
		async onLoad() {
			const res=await this.$u.api.homeContent()
			this.advertiseList=res.advertiseList
			this.SubjectList=res.subjectList
		},
		methods: {
			navClick(url){
				uni.navigateTo({
					url
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	@import "./index";
</style>
